/* Angular-UI-Bootstrap FIX */
.nav,
.pagination,
.carousel,
.panel-title a {
  cursor: pointer;
}
.page-header {
  margin-top: 0;
}
.page-header h3 {
  margin-top: 0;
}
.timeEntry {
  position: relative;
}
.timeEntry .time-actions {
  opacity: 0.1;
  -webkit-transition: opacity ease .2s;
  -moz-transition: opacity ease .2s;
  -o-transition: opacity ease .2s;
  transition: opacity ease .2s;
}
.timeEntry:hover .time-actions {
  opacity: 1;
}
.timeEntry .time-actions ul {
  margin-bottom: 0;
}
.timeEntry .time-actions ul li {
  padding-right: 3px;
  padding-left: 3px;
}
.timeEntry .time-actions a {
  color: #333;
}
.timeEntry .average-speed-container {
  margin-bottom: 0;
  font-weight: 100;
}
.timeEntry .average-speed-container .average-speed {
  font-size: 32px;
}
.timeEntry .distance {
  margin-right: 10px;
}
.panel-info {
  margin-bottom: 5px;
}
.chart-tab {
  margin-top: 20px;
}
